}

button,
[type=button],
[type=submit] {
  /*↓リセットCSS */
  margin: 0;
  background-image: none;
  width: auto;
  display: inline-block;
  text-decoration: none;
  border: none;
  border-radius: 0;
  /*↓カスタム */
  padding: 1.25rem 4.5rem;
  background: transparent linear-gradient(90deg, #7633a4 0%, #250d65 100%) 0% 0% no-repeat padding-box;
  color: white;
  font-size: 0.875rem;
  cursor: pointer;
  transition: 0.3s ease;
  border-radius: 3.125rem;
  line-height: 1;
}

[type=checkbox] {
  display: none;
}
[type=checkbox] + span {
  display: block;
  position: relative;
  padding: 0.625rem 0 0 1.375rem;
  margin: 0 0.75rem 0 0;
  font-size: 1rem;
  line-height: 1;
  cursor: pointer;
  transition: 0.3s ease;
}
[type=checkbox] + span::before {
  position: absolute;
  content: "";
  top: 74%;
  left: 0;
  transform: translateY(-50%);
  background: #fff;
  border: 1px solid #806c65;
  border-radius: 0.25rem;
  width: 0.875rem;
  height: 0.875rem;
  display: block;
}
[type=checkbox] + span::after {
  opacity: 0;
  transition: 0.3s ease;
}
[type=checkbox]:checked + span::after {
  content: "";
  position: absolute;
  top: 74%;
  left: 0.0625rem;
  display: block;
  opacity: 1;
  /*チェックマークじゃない場合は以下を変更する*/
  transform: translateY(-50%) rotate(-35deg);
  width: 0.6125rem;
  height: 0.30625rem;
  margin-top: -0.1em;
  margin-left: 0.1em;
  border-left: 2px solid #c94800;
  border-bottom: 2px solid #c94800;
}

.form__lastCheck [type=checkbox] + span::before {
  top: 27%;
}
@media screen and (min-width: 768px) {
  .form__lastCheck [type=checkbox] + span::before {
    top: 50%;
  }
}

.form__lastCheck [type=checkbox]:checked + span::after {
  top: 0.5625rem;
  left: 0.1875rem;
  transform: translateY(-50%) rotate(-48deg);
  width: 0.875rem;
  height: 0.525rem;
}

select {
  /*↓リセットCSS */
  border-radius: 0;
  font-family: inherit;
  color: inherit;
  border: none;
  cursor: pointer;
  width: 100%;
  /*↓カスタム */
  max-width: 30rem;
  margin-top: 0.9375rem;
  font-weight: 400;
  font-size: 1rem;
  padding: 0.40625rem 1rem;
  background-color: #ffffff;
  border: 1px solid #806c65;
  border-radius: 0.75rem;
  /*↓デフォルトのアイコンを消す場合はコメントアウト（noneにする）*/
  appearance: none;
  /*↓デフォルトではないアイコンを使う場合は入力する*/
  background-image: url(../images/common/form.svg);
  background-position: right 7px center;
  background-repeat: no-repeat;
}
select:focus {
  border: 1px solid #0d2936;
}
